.tech {
    width: 50%;
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
}

.developer {
    width: 50%;
    font-family: Roboto, sans-serif;
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
}

.techHeader {
    width: 50%;
    color: #000;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.developerHeader {
    width: 50%;
    color: #000;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.row {
    display: flex;
    padding: 10px 17px;
    width: 40vw;
    border-top: #E5E5E5 solid 1px;
}

.rowHeader {
    text-transform: capitalize;
    display: flex;
    background-color: #E5E5E5;
    padding: 17px;
    width: 40vw;
}

.loading {
    position: absolute;
    padding-left: 20vw;
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.8);
    content: '';
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
}

